.cards-container {
    width: 100%;
    height: auto;
    max-height: 80%;
    margin: 0 auto;
    text-align: center;
}

.cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1000px));
    list-style: none;
    margin: 0;
    padding: 0 0 20vh 0;
}

.cards_item {
    /* display: flex; */
    padding: 10%;
    padding-bottom: 0;
    
    filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.35));
}

.card_image img {
    width: 70%;
    height: 70%;
    object-fit: inherit;
    pointer-events: none;
    /* padding: 2vh 0 2vh 0; */
}

.card_image_h {
    display: block;
}

.card_image_v {
    display: none;
}

.card {
    background-color: white;
    border-radius: 0.25rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding-left: 30px;
    background: repeating-linear-gradient(#0000 0 calc(1.2rem - 1px), #66afe1 0 1.2rem) right bottom /100% 100%, linear-gradient(red 0 0) 30px 0/2px 100% #fff;
    background-repeat: no-repeat;
    line-height: 1.2rem;
    -webkit-mask: radial-gradient(circle .8rem at 2px 50%, #0000 98%, #000)0 0/100% 2.4rem;
}

.cards_item::after {
    content: "";
    position: absolute;
    top: 100%;
    height: 2vh;
    width: 100%;
    right: 0;
    background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/23618/rip.svg) bottom;
    background-size: 200%;
}

.card_content {
    padding: 0 15% 0 15%;
    margin: 15px;
}

h1.card_location,
h2.card_title,
h2.card_initials,
h2.card_year,
p {
    margin: 1.2rem 0;
    color: #000;
}

h1.card_location {
    font-family: 'ColoredPencil';
    font-size: 3vh;
}

h2.card_title {
    font-size: 5vh;
    line-height: 5vh;
    font-family: 'ColoredPencil';
}

h2.card_year {
    font-size: 3vh;
    line-height: 0.5vh;
    font-family: 'ColoredPencil';
}

h2.card_initials {
    font-size: 2.5rem;
    font-family: 'Antihero';
    color: orangered;
}

p.card_text {
    padding-top: 10px;
    font-family: 'ColoredPencil';
    font-size: 4vh;
    line-height: 5vh;
    padding: 2vw;
}

p.card_link {
    font-family: 'ColoredPencil';
    font-size: 3vh;
    line-height: 3rem;
    color: darkblue;
}

p.card_link:hover {
    color: blue;
    text-decoration: underline;
}

p.card_link:visited {
    color: indigo;
}

@media only screen and (max-width: 1440px) {
    .cards {
        grid-template-columns: repeat(auto-fit, minmax(500px, 800px));
    }
}

@media only screen and (max-width: 1024px) {
    .cards {
        grid-template-columns: repeat(auto-fit, minmax(400px, 700px));
    }

    .card_content {
        /* margin: 0; */
        padding: 0;
    }

    .cards_item::after {
        display: none;
    }

    h1.card_location {
        font-size: 2.5vh;
    }

    .card_image img {
        /* width: 70%; */
        height: auto;
        object-fit: fill;
        pointer-events: none;
        padding: auto;
    }

    .card_image_h {
        display: none;
    }

    .card_image_v {
        display: block;
        padding: 0;
        margin: 0 3vh 0 3vh;
    }

    p.card_text {
        font-size: 7vw;
        padding: 2vw 7vw 2vw 7vw;
    }

    p.card_link {
        font-size: 2vh;
        margin-top: 0;
        line-height: normal;
    }
}

@media only screen and (max-width: 720px) {
    .cards {
        grid-template-columns: repeat(auto-fit, minmax(300px, 600px));
    }
}

@media only screen and (max-width: 512px) {
    .cards {
        grid-template-columns: repeat(auto-fit, minmax(200px, 400px));
    }
}

@keyframes cd-enter-right {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }

    100% {
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes cd-enter-left {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }

    100% {
        opacity: 1;
        transform: translateX(0%);
    }
}