.desktop-only {
    display: block;
}

.portable-only {
    display: none;
}

.explore-title {
    font-family: "GoodBrush";
    font-size: 7vh;
    padding: 5vh;
}

.explore-btn {
    text-align: center;
    margin-top: 2vh;
    /* margin-bottom: 2vh; */
    padding: 3vh;
    border-radius: 5px;
    font-family: "GoodBrush";
    font-size: 3vh;
    color: #1d1d1d;
    background-color: #FFF;
}

@media only screen and (max-width: 1440px) {
    .desktop-only {
        display: none;
    }

    .portable-only {
        display: block;
    }
}