.web {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: flex-start;
    padding: 0.2rem calc((5vw - 50px) / 2);
    z-index: 1;
}

.initials h2 {
    font-size: 1.5vw;
    color: navy;
}

.initials-k {
    font-family: "KGHAPPY";
}

.initials-a {
    font-family: "FatWandals";
}

.web-option {
    font-size: 1.5vh;
    margin-right: 0.7vh;
    line-height: 1.5rem;
}

.web-option a {
    text-decoration: none;
    color: navy;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.option-icon {
    display: flex;
    align-items: center;
    margin-right: 4px;
}

.web-option:last-child {
    margin-right: 0px;
}

.lng-toggle {
    position: absolute;
    top: 10px;
    right: 30px;
    /* z-index: 9; */
}

@media only screen and (max-width: 1024px) {
    .initials h2 {
        font-size: 5vh;
    }
}