.desktop-only {
    display: block;
}

.portable-only {
    display: none;
}

.project-card {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    margin: 16px 0px;
    /* padding: 8px 0px; */
    /* box-shadow: 0px 0px 100px 200px rgba(0,0,0,.5); */
}

.table {
    height: 100%;
}

.row-eq-height {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
}

.project-photo {
    max-width: 85%;
    max-height: 85%;
    margin: auto;
    padding: 30px;
    max-height: 500px;
    max-width: 600px;
}

.project-title {
    font-size: 5vh;
    padding: 2vh;
    font-family: "SprayPaint";
    color: white;
    margin: auto;
    text-align: center;
}

.project-tags {
    display: inline-block;
    line-break: normal;
    margin: 1vh;
    padding: 1vh;
    max-width: 100%;
}

.project-desc {
    color: #FFF;
    line-height: normal;
    padding: 2vh;
    font-size: large;
}

.project-link {
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
}

.css-bhp9pd-MuiPaper-root-MuiCard-root {
    /* background-color: #0d2330 !important; */
}

.tag {
    border: solid 1px #FFF;
    /* word-break: keep-all;
    flex-wrap: nowrap; */
    /* overflow-wrap: normal; */
    /* white-space: nowrap; */
    padding: 1vh 2vh 1vh 2vh;
    margin: 1vh;
    align-items: center;
    color: rgb(255, 255, 255);
    font-size: 0.8vmax;
    background-color: rgba(7, 182, 245, 0.336);
    border-radius: 25px;
}

.project-action-btn {
    margin-top: 2vh;
    padding: 2vh;
    width: 250px;
    border-radius: 5px;
    color: #cacaca;
    font-size: 0.7vw;
    background-color: #272828;
}

.project-action-btn:hover {
    color: #FFF;
    background-color: #3f3f3f;
}

@media only screen and (max-width: 1440px) {
    .desktop-only {
        display: none;
    }

    .portable-only {
        display: block;
    }

    .project-tags {
        margin-right: 5vh;
        margin-left: 5vh;
        padding: 0px;
        display: inline-flex;
        text-align: center;
        align-items: center;
    }

    .tag {
        font-size: 1.7vh;
        margin-right: 0.7vh;
        margin-left: 0.7vh;
    }
}

@media only screen and (max-width: 1024px) {
    .project-title {
        font-size: 4vh;
        padding-top: 3vh;
    }

    .project-photo {
        width: 70%;
    }

    .project-card {
        flex-direction: column-reverse;
    }

    .project-info {
        margin-left: 0px;
        margin-top: 12px;
    }

    .project-desc {
        padding: 1vh;
        font-size: 1.7vmax;
        text-align: center;
        line-height: normal;
    }


    .project-action-btn {
        margin-top: 1vh;
        font-size: 1.5vh;
    }
}