.flicking-desktop {
    display: block;
}

.flicking-mobile {
    display: none;
}

.exp-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

/* Rewrite mui Card Style */
.exp {
    display: flex;
    flex-direction: column;

    max-width: 400px;
    height: auto;
    inline-size: fit-content;
    margin: 10px;
    padding: 20px;
    align-items: center;
    text-align: center;
    color: #FFF !important;
    background-color: #1b1f25 !important;
}

.role-name {
    font-family: 'GoodBrush';
    color: #ff0040 !important;
    padding: 1vh 5vh 1vh 5vh;
    font-size: 5vh;
}

.company-name {
    font-family: 'KGHAPPY';
    color: #fdafc3;
    font-size: 2.5vh;
    padding-bottom: 15px;
}

.experience-desc {
    font-size: 1.8vh;
}

h4.exp-details {
    font-family: 'ElegantTypewriter';
    font-size: 1.8vh;
}

.experience-notice {
    display: flex;
    justify-content: center;
    max-width: 100vw;
    margin: auto;
    padding: 20px 0 20px 0;
    text-align: center;
}

.exp-stack {
    margin: 10px !important;
    padding: 10px !important;
    background-color: #222831 !important;
    width: 7%;
    height: 7%;
}

.exp-logo {
    width: 20%;
    height: 20%;
}

@media only screen and (max-width: 1024px) {
    .flicking-desktop {
        display: none;
    }
    
    .flicking-mobile {
        display: block;
    }
    
    .exp {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 6vw;
        padding: 4vw;
    }

    .experience-notice {
        padding: 1vh;
        font-size: 1.5vmax;
        text-align: center;
        line-height: normal;
    }
}