.projects-container {
    /* display: flex; */
    /* background-color: #f1f1f1; */
    /* border: 1px solid #ccc; */
    margin: 0;
    padding: 0;
    width: 95%;
    height: 95%;
}

.selectors {
    height: auto;
    background-color: #727272;
}

/* Rewrite mui Card Style */
.selector {
    display: flex;
    height: auto;
    width: 22vh;
    white-space: nowrap;
    margin: 1vh !important;
    /* padding-bottom: 0px !important; */
    align-items: center;
    text-align: center;
    justify-content: center;
    color: #FFF !important;
    background-color: #000 !important;
    border: solid 3px #000 !important;
}

.project-icon {
    max-width: 2vw;
    max-height: 2vw;
    margin: 1vh;
    position: relative;
    float: left;
}

.MuiCardContent-root {
    padding: 0 !important;
}

.selector:active {
    border: solid 3px #009b1a !important;
}

.visualizers {
    height: inherit;
}

.current-viz {
    width: 100%;
    height: 90%;
    margin: 0px;
}

.visualizer {
    text-align: center;
    margin: 1vw 2vw 2vw 2vw;
    padding: 0 2vw 2vw 2vw;
    height: 100% !important;
    color: #FFF !important;
    background-color: #000 !important;
    border: solid 3px #FFF !important;
}

.projet-small-title {
    font-size: 0.7vmax;
    padding: 20px 0;
    color: #FFF;
    position: relative;
}

.css-bhp9pd-MuiPaper-root-MuiCard-root {
    color: #FFF !important;
}

@media only screen and (max-width: 1024px) {
    .MuiCardContent-root {
        padding: 0 !important;
    }
    
    .project-icon {
        max-width: 5vh;
        max-height: 5vh;
        margin: 1vh;
        position: relative;
        float: left;
    }

    .visualizers {
        height: 90%;
    }

    .visualizer {
        padding: 0 1vw 1vw 1vw;
    }
    
    .projet-small-title {
        font-size: 1.5vmax;
    }

}
