.loading-page {
  background: linear-gradient(180deg, #1C2269 50%, #18144F 100%) !important;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  display: flex;
}

.loading-prompt {
  font-family: "Antihero";
  color: orangered;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  margin: auto;
  padding: auto;
}
